import _ from 'lodash';

const initialState  = {
  docs :[],
  apis:[]
} 
function documents(state = initialState, action={}) {
  switch (action.type) {
  case 'GET_DOCES_SUCCESS':
    console.log('GET_DOCES_SUCCESS ==>', state, action.payload)
    var p = {...state};
    p.docs = action.payload;
    return p;

  default:
    return state;
  }
}

export default documents;


