import { takeLatest, takeEvery, select, put, call, fork, take, all } from 'redux-saga/effects';
import { getDoces } from './document';




export default function* rootSaga() {
  yield all([
    takeLatest('REQ_GET_DOCES', getDoces),
  ]);
}



